.Burger{
    margin: auto;
     width: 100%;                    /*  moblibe version */
    height: 250px;
    overflow: scroll;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
}

@media (min-width: 1000px) and (min-height:700px){                  /* laptop version */
    .Burger{
        width: 700px;
        height: 600px;
    }
   
}

@media (min-width: 500px) and (min-height:401px){               /* more smaller version of laptop */
    .Burger{
        width: 450px;
        height: 400px;
    }
   
}

@media (min-width: 500px) and (min-height:400px){              /* potrait version */
    .Burger{
        width: 350px;
        height: 300px;
        overflow: auto;
    }
   
}


