.Input{
    width: 100%;
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
}

.Label{
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
}

.InputElement{
    outline: none;
    border: 1px solid #ccc;
    background-color: white;
    font: inherit;
    padding: 6px 10px;
    width: 80%;
}

.InputElement:focus{
    outline: none;
    background-color: #ccc;
}

.InValid{
   border: 1px solid red;
   background-color: #FDA49A;
}


